<template>
  <div class="public">
    <header-content></header-content>
    <div class="public-content">
      <left :curid="curid" :isPull="isPull" @pull="pull"></left>
      <div class="right">
        <div class="right-content">
          <!-- 头部 -->
          <div class="title">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-shouye" />
            </svg>
            <p>
              你现在所在的位置是:
              <span>产品管理</span>
            </p>
          </div>

          <!-- 产品搜索 -->
          <div class="sreach">
            <div class="search-line">
              <label for="search">产品名：</label>
              <el-input v-model="searchObj.name" placeholder="请输入内容" clearable></el-input>
            </div>
            <div class="search-line">
              <el-select v-model="searchObj.product_id" slot="prepend" placeholder="分类" clearable>
                <el-option label="智慧阅读" value="1"></el-option>
                <el-option label="智能穿戴" value="2"></el-option>
                <el-option label="数据库" value="3"></el-option>
              </el-select>
            </div>
            <div class="search-line">
              <el-select v-model="searchObj.state" slot="prepend" placeholder="状态" clearable>
                <el-option label="下架" value="0"></el-option>
                <el-option label="上架" value="1"></el-option>
              </el-select>
            </div>
            <el-button
              type="primary"
              icon="el-icon-search"
              class="sreach-btn btn"
              @click="searchValue"
            >搜索</el-button>
          </div>

          <!-- 操作 -->
          <div class="operate">
            <el-button
              type="primary"
              icon="el-icon-success"
              class="sreach-btn"
              @click="operate(1)"
            >批量上架</el-button>
            <el-button
              type="primary"
              icon="el-icon-remove"
              class="sreach-btn"
              @click="operate(0)"
            >批量下架</el-button>
            <el-button type="primary" icon="el-icon-delete" class="sreach-btn" @click="delAll">批量删除</el-button>
            <el-button type="primary" icon="el-icon-circle-plus" class="sreach-btn" @click="add">增加</el-button>
          </div>

          <!-- 产品列表 -->
          <el-table
            class="pro-table"
            :data="tableData"
            border
            style="width: 100%"
            ref="poductTable"
            @selection-change="selectChange"
          >
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column prop="id" label="编号" width="100"></el-table-column>
            <el-table-column label="产品">
              <template slot-scope="scope">
                <a
                  :href="RespondURL.url+'/html/product.html?classify='+ (tableData[scope.$index].product_id | gochange) +'&id='+ tableData[scope.$index].details_id"
                >{{tableData[scope.$index].name}}</a>
              </template>
            </el-table-column>
            <el-table-column prop="state" sortable label="状态" width="120"></el-table-column>
            <el-table-column prop="product_id" sortable label="分类" width="150"></el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <i class="el-icon-edit-outline" title="编辑" @click="edit(scope.$index)"></i>
                <i class="el-icon-success" title="上架" @click="up(scope.$index)"></i>
                <i class="el-icon-remove" title="下架" @click="down(scope.$index)"></i>
                <i class="el-icon-delete" title="删除" @click="del(scope.$index)"></i>
                <i class="el-icon-folder-add" title="加入轮播" @click="addLunBo(scope.$index)"></i>
                <i class="el-icon-s-claim" title="加入首页轮播" @click="addHomeomeLunBo(scope.$index)"></i>
              </template>
            </el-table-column>
          </el-table>

          <!-- 分页 -->
          <div class="page">
            <el-pagination
              v-if="pageshow"
              layout="prev, pager, next"
              :total="total"
              @current-change="pageChange"
              @prev-click="pageChange"
              @next-click="pageChange"
              background
              :current-page="page"
            ></el-pagination>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import HeaderContent from "@/components/HeaderContent.vue";
import Left from "@/components/Left.vue";
export default {
  name: "Product",
  components: { Left, HeaderContent },
  created() {
    this.getList(1);
  },
  data() {
    return {
      pageshow: true,
      page: 1,
      curid: "0",
      searchObj: {
        product_id: "", //搜索分类
        name: "", //搜索名字
        state: "" //搜索状态
      },
      total: 0, // 总个数
      classify: ["智慧阅读", "智能穿戴", "数据库"], //分类
      stateArr: ["下架", "上架"], // 状态
      tableData: [], //列表数据
      checked: [], //被选中的数据
      flag: false,
      isPull: false
    };
  },
  filters:{
    gochange(val){
      console.log(val)
    }
  },
  methods: {
    // 获取列表
    getList(num) {
      this.$ajax
        .get(this.RespondURL.url + "Content2/findgoods/" + num)
        .then(data => {
          data.data.result.list.forEach(val => {
            let num = parseInt(val.state);
            val.state = this.stateArr[num];
            let num2 = parseInt(val.product_id);
            val.product_id = this.classify[num2 - 1];
          });
          this.tableData = data.data.result.list;
          this.total = data.data.result.total;
          console.log(this.tableData);
        });
    },
    // 下拉
    pull() {
      if (this.isPull) {
        this.isPull = false;
      } else {
        this.isPull = true;
      }
    },
    // 获得搜索数据
    getSearch(index) {
      this.flag = true;
      let obj = this.searchObj;
      this.$ajax
        .post(this.RespondURL.url + "Content2/likeselect/" + index, obj)
        .then(data => {
          data.data.result.list.forEach(val => {
            let num = parseInt(val.state);
            val.state = this.stateArr[num];
            let num2 = parseInt(val.product_id);
            val.product_id = this.classify[num2 - 1];
          });
          this.tableData = data.data.result.list;
          this.total = data.data.result.total;
        });
    },
    // 搜索按钮
    searchValue() {
      this.page = 1;
      this.pageshow = false;
      this.getSearch(1);
      this.$nextTick(() => {
        this.pageshow = true;
      });
    },
    // 选中
    selectChange(data) {
      this.checked = data;
    },
    //编辑
    edit(index) {
      this.$router.push({
        path: "/productChange",
        query: {
          id: [this.tableData[index].details_id]
        }
      });
    },
    // 加入产品轮播
    addLunBo(index) {
      let state = "";
      this.stateArr.forEach((val, num) => {
        if (val == this.tableData[index].state) {
          state = num;
        }
      });
      let classify2 = "";
      this.classify.forEach((val, sum) => {
        if (val == this.tableData[index].product_id) {
          classify2 = sum + 1;
        }
      });
      let obj = {
        name: this.tableData[index].name,
        classify: classify2,
        state: state,
        details_id: this.tableData[index].details_id,
        goodsId: this.tableData[index].id,
        img: this.tableData[index].img
      };
      this.$ajax
        .post(this.RespondURL.url + "GoodsCarousel/insertgoodscarousel", obj)
        .then(data => {
          if (!data.data.flag) {
            this.$alert("已加入轮播图,请选择其他", "提示", {
              confirmButtonText: "确定",
              callback: () => {}
            });
          } else {
            this.$alert(data.data.describe, "提示", {
              confirmButtonText: "确定",
              callback: () => {}
            });
          }
        });
    },
    // 加入首页轮播
    addHomeomeLunBo(index) {
      let state = "";
      this.stateArr.forEach((val, num) => {
        if (val == this.tableData[index].state) {
          state = num;
        }
      });
      let obj = {
        name: this.tableData[index].name,
        classify: this.tableData[index].product_id,
        state: state,
        details_id: this.tableData[index].details_id,
        fatherid: this.tableData[index].id,
        img: this.tableData[index].img
      };
      this.$ajax
        .post(this.RespondURL.url + "Slideshow/insertslideshow", obj)
        .then(data => {
          console.log(data);
          if (!data.data.flag) {
            this.$alert("已加入首页轮播图,请选择其他", "提示", {
              confirmButtonText: "确定",
              callback: () => {}
            });
          } else {
            this.$alert(data.data.describe, "提示", {
              confirmButtonText: "确定",
              callback: () => {}
            });
          }
        });
    },
    // 上架
    up(index) {
      let obj = {
        id: [this.tableData[index].id],
        details_id: [this.tableData[index].details_id]
      };
      this.$ajax
        .post(this.RespondURL.url + "Content2/goodsshelves", obj)
        .then(data => {
          if (data.data.flag) {
            this.tableData[index].state = "上架";
          }
        });
    },
    // 下架
    down(index) {
      let obj = {
        id: [this.tableData[index].id],
        details_id: [this.tableData[index].details_id]
      };
      this.$ajax
        .post(this.RespondURL.url + +"Content2/soldout", obj)
        .then(data => {
          if (data.data.flag) {
            this.tableData[index].state = "下架";
          }
        });
    },
    // 批量操作
    operate(index) {
      if (this.checked.length == 0) {
        return;
      }
      let idArr = [];
      let idArr2 = [];
      this.checked.forEach(val => {
        idArr.push(val.id);
        idArr2.push(val.details_id);
      });
      let obj = {
        id: idArr,
        details_id: idArr2
      };
      let repURL = "";
      if (index == 1) {
        //上架
        repURL = this.RespondURL.url + "Content2/goodsshelves";
      } else {
        //下架
        repURL = this.RespondURL.url + "Content2/soldout";
      }
      this.$ajax.post(repURL, obj).then(data => {
        if (data.data.flag) {
          this.$alert(data.data.describe, "提示", {
            confirmButtonText: "确定",
            callback: () => {
              this.tableData.forEach(val => {
                this.checked.forEach(e => {
                  if (e.id == val.id) {
                    val.state = this.stateArr[index];
                  }
                });
              });
              //选中清除
              this.checked = [];
              let refsElTable = this.$refs.poductTable;
              refsElTable.clearSelection();
            }
          });
        }
      });
    },
    // 删除
    del(index) {
      let obj = {
        id: [this.tableData[index].id],
        details_id: [this.tableData[index].details_id]
      };
      let img = {
        fileurl: [this.tableData[index].img]
      };
      this.$confirm("此操作将删除这些活动, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          console.log(obj);
          this.$ajax
            .post(this.RespondURL.url + "Content2/deletegoods", obj)
            .then(data => {
              console.log(data);
              if (data.data.flag) {
                this.tableData.splice(index, 1);
              }
            });
          this.$ajax
            .post(this.RespondURL.url + "setmeal/deletefile", img)
            .then(data => {
              if (!data.data.flag) {
                console.log(data);
              }
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    // 批量删除
    delAll() {
      if (this.checked.length == 0) {
        return;
      }
      let arr = this.tableData.map(x => x);
      let idArr = [];
      let idArr2 = [];
      let imgArr = [];
      this.checked.forEach(val => {
        idArr.push(val.id);
        idArr2.push(val.details_id);
        imgArr.push(val.img);
        arr.forEach((value, index) => {
          if (val.id == value.id) {
            arr.splice(index, 1);
          }
        });
      });
      let obj = {
        id: idArr,
        details_id: idArr2
      };
      let imgobj = {
        fileurl: imgArr
      };
      this.$confirm("此操作将删除这些活动, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          //删除产品
          this.$ajax
            .post(this.RespondURL.url + "Content2/deletegoods", obj)
            .then(data => {
              if (data.data.flag) {
                this.tableData = arr;
              }
            });
          //删除图片
          this.$ajax
            .post(this.RespondURL.url + "setmeal/deletefile", imgobj)
            .then(data => {
              if (!data.data.flag) {
                console.log(data);
              }
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    // 添加
    add() {
      this.$router.push("/productChange");
    },
    // 分页
    pageChange(data) {
      if (
        this.searchObj.name == "" &&
        this.searchObj.product_id == "" &&
        this.searchObj.state == ""
      ) {
        this.getList(data);
      } else {
        this.getSearch(data);
      }
    }
  }
};
</script>
<style lang="less">
.sreach {
  background: #f6fafd;
  padding: 20px 20px;
  text-align: left;
  margin-bottom: 20px;
  .search-line {
    width: 300px;
    padding-left: 60px;
    height: 40px;
    margin-bottom: 20px;
    display: inline-block;
    position: relative;
    label {
      display: block;
      position: absolute;
      line-height: 40px;
      left: 0;
    }
  }
  .btn {
    margin-left: 60px;
  }
}
</style>